import {	BarElement,	CategoryScale,	Chart as ChartJS,	ChartOptions,	Legend,	LinearScale,	TimeScale,	Title,	Tooltip,} from 'chart.js';// eslint-disable-next-line @typescript-eslint/ban-ts-comment// @ts-ignoreimport autocolors from 'chartjs-plugin-autocolors';import { observer } from 'mobx-react-lite';import React from 'react';import { Bar } from 'react-chartjs-2';import CategoriesStore from '../../../../store/CategoriesStore/categories-store';import styles from './Chart.module.scss';ChartJS.register(	CategoryScale,	LinearScale,	BarElement,	Title,	Tooltip,	Legend,	autocolors,	TimeScale,);export const Chart = observer((): React.ReactElement => {	const { chartData, labels } = CategoriesStore;	const data = {		labels: labels,		datasets: chartData,		borderWidth: 1,	};	const options = {		maintainAspectRatio: false,		scales: {			y: {				/*		stacked: true,*/				grid: {					display: true,					color: 'rgba(255,99,132,0.2)',				},			},			/*	x: {          grid: {            display: false,          },        },*/		},	};	return (		<div className={styles['chart']}>			{/*// eslint-disable-next-line @typescript-eslint/ban-ts-comment				// @ts-ignore*/}			<Bar options={options} data={data} />		</div>	);});